.bg.contacts-page {
  background: url("../img/bg-contacts.png") no-repeat bottom/cover , #fff; 
  @media screen and (max-width: $smDesktopWidth) {
    min-height: 60%; 

  }
}
.main-content.contacts-page {
  @media screen and (max-width: $smDesktopWidth) {
    height: calc(60vh - 78px);
  }
}
.contacts-page {
  .title {
    @media screen and (max-width: $smDesktopWidth) {
      display: none;
    }
  }
}

.contact-us {
  max-width: 1130px;
  display: flex;  
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  @media screen and (max-width: $smDesktopWidth) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .contact-us__title {
      text-align: center;
    }
    .contact-us__info {
      max-width: 100%;
    }
    .contact-us__form {
      padding-left: 0px;
    }
    .contact-us__submit {
      margin-top: 0px;
      margin-left: calc(50% - 88px);
      margin-right: calc(50% - 88px);
      margin-bottom: 140px;
    }
    .contact-us__file-name, .contact-us__delete-file {
      top: 5px;
    }
  }
  @media screen and (max-width: $phoneWidth) {
    .contact-us__fieldset {
      flex-direction: column;
      // background-color: red;
    }
    .contact-us__group {
      width: 100%;
      margin-bottom: 10px;
    }
    .contact-us__fieldset:nth-child(1) {
      .contact-us__group:nth-child(2) span {
        display: none;
      }
    }
    .contact-us__input {
      width: 100%; 
    }
    .contact-us__title {
      min-width: 100%;
    }
    .contact-us__text {
      font-size: 18px;
      min-width: 100%;
    }
    .empty {
      display: none;
    }
  }

  input, label{
    display:block;
  }
  &__info {
    max-width: 380px;
  }
  &__title {
    padding-bottom: 27px;
    text-align: left;
    font-size: 72px;
    font-weight: bold;
    color: #0d588e;
    min-width: 320px;
    @media screen and (max-width: $smDesktopWidth) {
      font-size: 52px;
    }
    @media screen and (max-width: $tableWidth) {
      font-size: 36px;
    }
  }
  &__text {
    font-size: 24px;
    font-weight: normal;
    color: #3c3c3c;
    min-width: 320px;
    padding-bottom: 40px;
  }


  &__form {
    width: 100%;
    padding-left: 90px;
  }
  &__fieldset {
    margin-bottom: 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  &__group {
    width: calc(50% - 7px);
    margin-bottom: 30px;
  }
  &__group-name {
    font-size: 24px;
    color: #3c3c3c;
    padding-bottom: 10px;
    display: block;
    @media screen and (max-width: $smDesktopWidth) {
      font-size: 20px;
    }
    // @media screen and (max-width: $tableWidth) {
    //   font-size: 18px;
    // }
  }
  &__group.textarea {
    width: 100%;
    position: relative;
  }
  &__input {
    min-width: 220px;
    width: 100%;
    height: 52px;
    border: solid 1px #3c3c3c;
    
  }
  &__textarea {
    min-width: 220px;
    width: 100%;
    min-height: 136px;
    overflow-y: hidden;
    border: solid 1px #3c3c3c;
    padding-right: 54px;
    z-index: 1;
  }
  &__label {
    padding-top: 9px;
    font-size: 14px;
    color: #767676;
  }
  &__attach-file {
    color: transparent;
    width: 24px;
    height: 24px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    top: 44px;
    right: 10px;
    z-index: 2;
    cursor: pointer;
    & + label {
      width: 24px;
      height: 24px;
      cursor: pointer;
      position: absolute;
      top: 44px;
      right: 10px;
      z-index: 1;
    }
  }
  
  &__file-name {
    display: none;
    position: absolute;
    top: 7px;
    right: 20px;
    font-size: 14px;
    color: #767676;
    // line-height: 24px;
  }
  &__delete-file {
    cursor: pointer;
    display: none;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 7px;
    right: 0px;
    &::before, &::after {
      content: '';
      position: absolute;
      top: 40%;
      right: 10%;
      display: block;
      height: 2px;
      width: 14px;
      background-color: #767676;
      border-radius: 2px;
    }
    &::before {
      transform: rotate(45deg);
    }
    &::after {
      transform: rotate(-45deg);
    }
  }

  &__submit {
    margin-top: 25px;
    width: 178px;
    height: 52px;
    background-color: #0d588e;
    font-size: 24px;
    color: #ffffff;
    margin-bottom: 240px;
    cursor: pointer;
  }


}