section.header {
  padding-top: 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  @media screen and (max-width: $smDesktopWidth) {
    padding-top: 40px;
    .desctop-menu {
      display: none;
    }
    .burger-menu {
      display: block;
    }
  }
  
  .logo {
    &-big {
      display: block;
    }
    &-small {
      display: none;
    }
    @media screen and (max-width: 576px) {
      &-big {
        display: none;
      }
      &-small {
        display: block;
      }
    }
    
  }
  
  .desctop-menu {
    & ul {
      display: flex;
      flex-direction: row;
    }
    & li {
      position: relative;
      border: 1px solid transparent;
      border-right: 1px solid black;
      &:last-child {
        padding-right: 0px;
        border: 1px solid transparent;
      }
    }
    & a {
      padding: 0 30px;
      font-size: 24px;
      color: #484848;
    }
    & li.current_page_item a{
      color: #0d588e;
    }
  }

  .burger-menu {
    display: none;
    &__button {
      position: absolute;
      top: 46px;
      right: 28px;
      z-index: 99;
      height: 24px;
      width: 24px;
      background-color: transparent;
    }
    &__lines {
      &, &::before, &::after {
        position: absolute;
        display: block;
        height: 3px;
        background-color: $default-color;
        border-radius: 2px;
      }
      & {
        width: 24px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &::before {
        content: '';
        top: -7px;
        left: 50%;
        width: 17px;
        transform: translate(-50%, 0%);
      }
      &::after {
        content: '';
        top: 7px;
        left: 50%;
        width: 17px;
        transform: translate(-50%, 0%);
      }
    }
    &__nav {
      transition: 0.8s;
      position: fixed;
      z-index: 88;
      display: flex;
      flex-flow: column;
      height: 100vh;
      background-color: #ffffff;
      top: 0;
      right: -100%;
      width: 300px;
      padding-top: 160px;
    }
    &__nav a {
      text-align: right;
      padding-right: 50px;
      padding-left: 7px;
      font-size: 24px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #484848;
      position: relative;
      
    }

    &__nav li.current_page_item a:before {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      left: -37px;
      height: 3px;
      width: 30px;
      border-radius: 2px;
      background-color: $default-color;
    }
    & ul {
      text-align: right;
      & li {
        padding-bottom: 25px;
      }
    }
    &__overlay {
      transition: 0.8s;
      display: none;
      position: fixed;
      top: 0;
      right: 0;
      width: 100vw;
      height: 100vh;
      z-index: 77;

    }
    &_active {
      .burger-menu__lines {
        background-color: transparent;
        &::before {
          width: 24px;
          top: 0;
          left: 0;
          transform: rotate(45deg);
        }
        &::after {
          width: 24px;
          top: 0;
          left: 0;
          transform: rotate(-45deg);
        }
      }
      .burger-menu__nav {
        right: 0;
      }
      .burger-menu__overlay {
        display: block;
        background-color: rgba(0, 0, 0, .8);
      }
      // .burger-menu__button {
      //   position: fixed;
      // }
    }
  }

  
}