section.footer {
  padding-bottom: 90px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .location, 
  .contacts {
    display: flex;
    flex-direction: column;
  }
  .location {
    align-items: flex-start;
  }
  .contacts {
    align-items: flex-end;
  }
  @media screen and (max-width: $phoneWidth) {
    padding-bottom: 50px;
    flex-direction: column;
    .location, 
    .contacts {
      align-items: center;
    }
  }
  .info {
    font-size: 24px;
    font-weight: bold;
    @media screen and (max-width: $tableWidth) {
      font-size: 18px;
      font-weight: normal;
    }
    color: #000000;
    padding-bottom: 6px;
  }
}