.bg.home-page {
  background: url("../img/bg-home.png") no-repeat bottom/cover;
}

.main-content.home-page {
  .button {
    margin-top: 50px;
    width: 100px;
    height: 100px;
    background-color: transparent;
    border: 2px solid #0d588e;
    border-radius: 50%;
    position: relative;
    transition: .1s;
    &__lines {
      position: absolute;
      top: 50%;
      right: 50%;
      width: 40px;
      transform: translate(50%, 50%);
      &::before, &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 35px;
        height: 2px;
        background-color: #0d588e;
        transform-origin: right center;
      }
      &::before {
        transform: rotate(30deg);
      }
      &::after {
        transform: rotate(-30deg);
      }
    }
    @media screen and (max-width: $smDesktopWidth) {
      margin-top: 30px;
      width: 80px;
      height: 80px;
      .button__lines {
        width: 35px;
        &::before, &::after {
          width: 30px;
        }
      }
    }
    @media screen and (max-width: $tableWidth) {
      width: 60px;
      height: 60px;
      .button__lines {
        width: 30px;
        &::before, &::after {
          width: 25px;
        }
      }
    }
    &:hover {
      background-color: #0d588e;
      .button__lines {
        &::before, &::after{
          background-color: #fff;
        }
      }
    }
  }
}