body {
	margin: 0 auto;
  font-family: $mainFont;
  background-color: #fff;
  font-size: $mainFontSize;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}
  
.wrapper {
	max-width: 1300px;
	padding: 0 40px;
	margin: 0 auto;
	@media screen and (max-width: $tableWidth) {
		padding: 0 20px;
	}
}


.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  z-index: -1;
}



.main-content {
  height: calc(100vh - 108px);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  .title {
    padding-top: 17vh;
    @media screen and (max-width: $smDesktopWidth) {
      padding-top: 20vh;
    }
    text-align: center;
    font-size: 60px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 3px;
    color: #0d588e;

    @media screen and (max-width: $smDesktopWidth) {
      font-size: 52px;
    }
    @media screen and (max-width: $tableWidth) {
      font-size: 36px;
    }
  }
}



